const videoPlayer = (videoSelector, btnSelector) => {
    const video = document.querySelector(videoSelector);
    const btn = document.querySelector(btnSelector);
    const playerIcons = btn.querySelectorAll('.player-icon');

    btn.addEventListener('click', () => {
        console.dir(video)
        console.log(playerIcons);
        if (btn.hasAttribute('play') && !video.ended) {
            video.pause();
            btn.classList.toggle('btn__about--overlay')
            playerIcons.forEach(item => {
                if (item.classList.contains('player-icon--play')) {
                    item.classList.remove('hide');
                } else {
                    item.classList.add('hide');
                }
            });
            btn.removeAttribute('play');
        } else {
            btn.setAttribute('play', 'true');
            btn.classList.toggle('btn__about--overlay')
            playerIcons.forEach(item => {
                if (!item.classList.contains('player-icon--play')) {
                    item.classList.remove('hide');
                } else {
                    item.classList.add('hide');
                }
            });
            video.play();
        }
    });
};

export default videoPlayer